.painting-construction {
	position: relative;

	&-wrap {
		padding: 105px 30px 105px 85px;
		position: relative;

		@include md {
			padding-left: 20px;
			padding-right: 20px;
			padding-top: 80px;
			padding-bottom: 50px;
		}

		&:before {
			content: "";
			display: block;
			width: 140px;
			height: 110px;
			background-image: url("../img/bird.png");
			position: absolute;
			right: 10px;
			top: 200px;
		}

		&:after {
			width: 44%;
			@include lg {
				width: 40%;
			}
		}
	}

	&__img {
		@include lg {
			margin-bottom: 50px;
		}
	}

	&:before {
		content: "";
		display: block;
		position: absolute;
		z-index: -19;
		width: 100%;
		height: 700px;
		max-width: 2000px;
		left: 50%;
		transform: translateX(-50%);
		top: -200px;
		background-image: url("../img/clouds.png");
		background-repeat: no-repeat;
		background-size: cover;
		background-position-x: left;
	}

	&:after {
		content: "";
		display: block;
		position: absolute;
		z-index: -20;
		width: 100%;
		height: 100%;
		top: 250px;
		left: 0;
		max-width: 2000px;
		left: 50%;
		transform: translateX(-50%);
		background-image: url("../img/painting-construction-bg.png");
		background-repeat: no-repeat;
		background-size: cover;
		background-position: top center;
	}

	&-heading {
		margin-bottom: 30px;

		&__title {
			@include lg {
				font-size: 32px;
				line-height: 1.2;
				margin-bottom: 10px;
				@include md {
					font-size: 23px;
				}
			}
		}
		&__subtitle {
			margin-bottom: 32px;
			@include lg {
				font-size: 27px;
				line-height: 1.2;
				margin-bottom: 15px;
				@include md {
					font-size: 20px;
				}
			}
		}
		&__text {
			font-size: 18.72px;
			text-align: center;
			width: 70%;
			margin-left: auto;
			margin-right: auto;
			line-height: 22.46px;
			@include lg {
				font-size: 15px;
				line-height: 1.2;
				@include md {
					font-size: 15px;
				}
			}
		}
	}

	&-first-row {
		display: flex;
		@include lg {
			flex-wrap: wrap;
			& .construction-element {
				margin-bottom: 50px;
			}
		}
	}

	&-img-col {
		width: 55%;
		padding-top: 15px;
		@include lg {
			width: 100%;
			padding-left: 0;
		}
	}

	&-inf-col {
		width: 45%;
		padding-left: 20px;
		@include lg {
			width: 100%;
			padding-left: 0;
		}
	}

	&-second-row {
		display: flex;
		align-items: flex-end;
		@include lg {
			flex-wrap: wrap;
			& .painting-construction-col:first-child .construction-element{
				margin-bottom: 50px;
			}
		}

		.painting-construction-col {
			&:nth-child(1) {
				width: 42%;
				@include lg {
					width: 100%;
				}
			}
			&:nth-child(2) {
				width: 58%;
				@include lg {
					width: 100%;
				}
			}
		}
	}

}

.construction-element {
	display: flex;
	align-items: flex-end;
	position: relative;

	@include lg {
		align-items: center;
		@include md {
			width: 100%;
			@include sm {
				flex-direction: column;
			}
		}
	}

	&__title {
		margin-bottom: 13px;
		font-size: 24px;
		font-weight: bold;
		@include sm {
			text-align: center;
		}
	}

	&__inf {
		padding-left: 15px;
		@include sm {
			padding-left: 0;
		}
	}

	&__list {
		@include sm {
			display: flex;
			flex-direction: column;
			justify-content: space-around;
		}

		&-item {
			padding-left: 10px;
			position: relative;
			margin-bottom: 7px;
			font-size: 15px;

			&:before {
				content: "";
				display: block;
				width: 5px;
				position: absolute;
				height: 5px;
				background-color: #000;
				border-radius: 50%;
				left: 0;
				@include centerY;
			}
		}
	}

	&__img {
		width: 110px;
		height: 110px;
		overflow: hidden;
		background-color: #ADAFBE;
		border-radius: 50%;
		border: 5px solid #fff;
		cursor: pointer;

		&-big {
			width: 210px;
			height: 210px;
			@include xl {
				width: 180px;
				height: 180px;
				@include lg {
					width: 110px;
					height: 110px;
				}
			}
		}

		@include lg {
			img {
				width: 100%;
			}
			@include sm {
				width: 150px;
				height: 150px;
				margin-bottom: 20px;
			}
		}
	}

	&-1 {
		margin-bottom: 100px;
		@include xl {
			margin-bottom: 50px;
			@include lg {
				margin-bottom: 0;
			}
		}
		img {
			width: auto;
			margin-left: 20px;
			margin-top: 30px;
		}		

		&:after {
			content: "";
			display: block;
			height: 45px;
			width: 200px;
			border-right: 1px dashed #7C7C7C;
			border-bottom: 1px dashed #7C7C7C;
			z-index: -1;
			position: absolute;
			bottom: -45px;
			left: -145px;
		}
	}

	&-2 {
		&:before {
			content: "";
			display: block;
			height: 100px;
			width: 1px;
			border-right: 1px dashed #7C7C7C;
			z-index: -1;
			position: absolute;
			bottom: -25px;
			left: 54px;
		}
		&:after {
			content: "";
			display: block;
			height: 60px;
			width: 160px;
			border-right: 1px dashed #7C7C7C;
			border-left: 1px dashed #7C7C7C;
			border-bottom: 1px dashed #7C7C7C;
			z-index: -1;
			position: absolute;
			bottom: -84px;
			left: -105px;
			z-index: 1;
			@include xl {
				width: 150px;
				left: -95px;
			}
		}
	}
	
	&-3 {
		&:after {
			content: "";
			display: block;
			height: 210px;
			width: 0px;
			border-left: 1px dashed #7C7C7C;
			z-index: -1;
			position: absolute;
			top: -220px;
			left: 48.5%;
			z-index: 1;
		}
	}

	&-4 {
		&:after {
			content: "";
			display: block;
			height: 165px;
			width: 200px;
			border-left: 1px dashed #7C7C7C;
			border-bottom: 1px dashed #7C7C7C;
			z-index: -1;
			position: absolute;
			bottom: 107px;
			left: -95px;
			z-index: -1;
		}
	}

	&:before, &:after {
		@include lg {
			display: none;
		}
	}
}