.get-picture {

	&-wrap {
		padding-top: 50px;
		position: relative;
		min-height: 300px;
		z-index: 1;

		@include lg {
			padding-top: 100px;
			padding-bottom: 50px;
		}
	}

	&-steps {
		display: flex;
		position: absolute;
		align-items: flex-start;
		bottom: -62px;
		@include centerX;

		@include lg {
			position: static;
			transform: translate(0,0);
			flex-direction: column;
			bottom: 0;
			align-items: center;
			width: 100%;
			margin-bottom: 50px;
		}
	}

	&__title {
		@include lg {
			margin-bottom: 30px;
		}
	}

	&-step {
		//width: 11.5%;
		min-width: 150px;
		text-align: center;
		display: flex;
		flex-direction: column;
		align-items: center;
		position: relative;

		@include xl {
			min-width: 130px;
			@include lg {
				flex-direction: row;
				align-items: center;
				padding-top: 20px;
				padding-bottom: 20px;
				min-width: 33%;
				@include md {
					width: 50%;
					@include sm {
						width: 70%;
						@include xs {
							width: 90%;
						}
					}
				}
			}
		}

		&:after {
			content: "";
			display: block;
			border: 6px solid transparent;
			border-left: 6px solid black;
			position: absolute;
			top: 97px;
			right: -9px;

			@include lg {
				display: none;
			}
		}

		&:last-child {
			&:after {
				display: none;
			}
		}

		&__img {
			margin-bottom: 20px;
			height: 70px;
			@include lg	{
				margin-bottom: 0;
				height: auto;
				margin-right: 20px;
				min-width: 70px;
			}
		}

		&__title {
			font-family: $ff-title;
			@include xl {
				font-size: 15px;
				@include lg {
					margin-left: auto;
				}
			}
		}

		&__number {
			margin-bottom: 20px;
			width: 24px;
			height: 24px;
			border-radius: 50%;
			background-color: #3B79E1;
			line-height: 24px;
			font-weight: bold;
			font-size: 14.33px;
			font-family: $ff-title;
			color: #fff;
			@include lg	{
				margin-bottom: 0;
				margin-right: 20px;
			}
		}
	}

	&__button {
		@include centerX;
		bottom: -175px;
		z-index: 5;
		@include lg {
			position: relative;
			bottom: 0;
			margin-left: auto;
			margin-right: auto;
			display: flex;
			justify-content: center;
		}
	}
}

.get-picture-step__img {
	&-1 {
		@include sprite($step-1)
	}
	&-2 {
		@include sprite($step-2)
	}
	&-3 {
		@include sprite($step-3)
	}
	&-4 {
		@include sprite($step-4)
	}
	&-5 {
		@include sprite($step-5)
	}
	&-6 {
		@include sprite($step-6)
	}
	&-7 {
		@include sprite($step-7)
	}

}