.form-block {

	&-wrap {
		padding-bottom: 112px;
		padding-top: 65px;

		&:after {
			width: 54%;

			border-top-left-radius: 110px;
			border-bottom-left-radius: 110px;
		}
	}

	&__title {
		width: 70%;
		margin-left: auto;
		margin-right: auto;
		text-align: center;
		
		line-height: 53.53px;
		margin-bottom: 25px;

		@include lg {
			font-size: 32px;
			line-height: 1.3;
			width: 95%;
			@include xs {
				font-size: 26px;
			}
		}
	}
	&__subtitle {
		width: 60%;
		font-size: 18.72px;
		text-align: center;
		margin-left: auto;
		margin-right: auto;
		line-height: 22.46px;
		margin-bottom: 80px;
		@include lg {
			font-size: 17px;
			line-height: 1.3;
			width: 95%;
		}
	}

	&-row {
		display: flex;
		justify-content: center;
		@include md {
			flex-wrap: wrap;
		}
	}

	&-left-col {
		width: 34%;
		margin-right: 50px;
		padding-top: 25px;
		@include xl {
			width: 45%;
			@include lg {
				margin-right: 25px;
				width: 50%;
				@include md {
					width: 100%;
					order: 1;
					margin-right: 0;
				}
			}
		}
	}

	&-right-col {
		width: 40%;
		@include xl {
			width: 45%;
			@include lg {
				width: 40%;
				@include md {
					width: 100%;
					margin-bottom: 50px;
				}
			}
		}
	}

	&__img {
		position: relative;
		&:after {
			content: "";
			display: block;
			width: 326px;
			height: 356px;
			background-image: url("../img/artboard-red.png");
			position: absolute;
			top: -240px;
			right: -140px;
			z-index: -1;
			@include xl {
				right: 0;
			}
		}
	}
}

.form {

	&-row {
		margin-bottom: 20px;
	}

	&-input {
		padding-left: 27px;
		padding-right: 27px;
		padding-top: 16px;
		padding-bottom: 16px;
		width: 100%;
		border: 1px solid #BABABA;
		border-radius: 10px;
		position: relative;

		&__block {
			width: 88%;
			position: relative;
			@include lg {
				width: 100%;
			}

			&[data-required="true"] {
				&:before {
					content: "*";
					color: #3b79e1;
					font-size: 28px;
					position: absolute;
					right: 15px;
					top: 12px;
					z-index: 10;
				}
			}
		}

		&:focus {
			border-color: #EF5055;
		}

		@include placeholder {
			// color: #A9A9A9;
			color: red;
			font-size: 50px;
		}
	}

	&-textarea {
		width: 88%;
		height: 240px;
		border: 1px solid #BABABA;
		border-radius: 10px;
		padding-left: 27px;
		padding-right: 27px;
		padding-top: 16px;
		padding-bottom: 16px;
		@include lg {
			width: 100%;
		}

		&:focus {
			border-color: #EF5055;
		}
	}

	&-policy {
		display: flex;
		@include xs {
			flex-wrap: wrap;
		}
		
		&__checkbox {
			margin-right: 16px;
		}
		&__link {
			margin-right: 57px;
			color: #723BCE;
			position: relative;
			@include xs {
				font-size: 14px;
			}

			&:after {
				content: "";
				display: block;
				height: 1%;
				width: 100%;
				background-color: #723BCE;
				bottom: 0;
			}
		}

		&__tooltip {
			position: relative;
			@include xs {
				width: 100%;
				text-align: right;
				order: -1;
				margin-bottom: 20px;
			}
			&:before {
				content: "*";
				color: #3b79e1;
				font-size: 28px;
				position: absolute;
				right: 120px;
				@include centerY;
				top: 15px;
			}
		}
	}

	&-button {
		margin-left: 22px;
		padding-top: 5px;
		@include md {
			margin-left: 0;
			display: flex;
			justify-content: center;
		}
	}
}