.causes {

	&-wrap {
		position: relative;
		padding-top: 50px;
		padding-bottom: 100px;

		@include md {
			padding-bottom: 50px;
		}

		&:before {
			content: "";
			display: block;
			width: 85px;
			height: 100px;
			border-right: $block-border-settings;
			border-bottom: $block-border-settings;
			border-bottom-right-radius: $br-block-border;		
			@include centerX;
			left: 46.9%;
			top: -100px;
			z-index: -1;
			@include lg {
				left: 45%;
				@include md {
					display: none;
				}
			}
		}

		&:after {
			width: 43.5%;
			@include lg {
				width: 40%;
			}
		}
	}

	&-row {
		display: flex;
		justify-content: space-around;
		align-items: flex-end;

		@include md {
			flex-wrap: wrap;
		}
	}

	&-col {
		width: 23%;
		&:nth-child(1) {
			.causes-item__img {
				margin-bottom: -20px;
			}
		}
		&:nth-child(2) {
			.causes-item__img {
				margin-bottom: 10px;
			}
		}

		@include md {
			width: 80%;
		}
	}

	&__title {
		margin-bottom: 10px;
		@include sm {
			line-height: 1.2;
		}
	}

	&-item {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include md {
			margin-bottom: 50px;
		}
		&__text {
			line-height: 21.32px;
			font-size: 16px;
			text-align: center;
			@include md {
				font-size: 20px;
			}
		}
	}

	&__button {
		@include centerX;
		bottom: -30px;
		z-index: 5;
	}
}