.about {

	&-wrap {
		//min-height: 605px;
		padding-top: 50px;
		padding-bottom: 100px;
		@include md {
			padding-bottom: 50px;
		}
	}

	&-row {
		display: flex;
		@include lg {
			flex-wrap: wrap;
		}
	}

	&-img-col {
		width: 46%;
		@include lg {
			width: 100%;
			display: flex;
			justify-content: center;
			margin-bottom: 50px;
		}
	}

	&__img {
		width: 100%;
		height: 100%;
		position: relative;
		@include lg {
			padding-left: 70px;
			width: auto;
			height: auto;
			@include sm {
				padding-left: 50px;
				@include xs {
					padding-left: 40px;
				}
			}
			&:after {
				content: "";
				display: block;
				width: 100%;
				height: 5px;
				background-color: white;
				position: absolute;
				bottom: -1px;
				left: 0px;
			}
		}
		img {
			position: absolute;
			margin-left: auto;
			display: block;
			bottom: -103px;;
			right: -40px;
			@include lg {
				margin-left: 0;
				position: relative;
				bottom: 0;
				right: 0;
			}
		}
	}

	&-text-col {
		width: 54%;
		padding-right: 50px;
		padding-left: 20px;

		@include lg {
			width: 80%;
			text-align: center;
			margin-left: auto;
			margin-right: auto;
			padding: 0;
			@include md {
				width: 90%;
			}
		}
	}

	&__button {
		display: flex;
		justify-content: center;
	}

	&__title {
		margin-bottom: 30px;
	}

	&-text {
		&__title {
			font-size: 24px;
			color: $fc-main-lighten-dark;
			font-weight: bold;
			font-family: $ff-title;
			margin-bottom: 20px;

			@include lg {
				font-size: 26px;
			}
		}

		&__descr {
			font-size: 15px;
			p {
				line-height: 1.2;
				margin-bottom: 12px;
				&:last-child {
					margin-bottom: 55px;
				}
				@include lg {
					font-size: 17px;
					@include sm {
						font-size: 15px;
					}
				}
			}
		}
	}
}