.preview{
	padding-top: 60px;
	padding-bottom: 110px;
	background-image: url("../img/preview-bg.png");
	background-repeat: no-repeat;
	background-position: top center;

	@include sm {
		padding-bottom: 80px;
	}

	&__decor-blue-circle {
		position: absolute;
		@include centerX;
		bottom: -60px;
		@include md {
			display: none;
		}
		z-index: 2;
	}

	&-wrap {
		position: relative;
	}

	&-first-row {
		display: flex;
		align-items: flex-start;
	}

	&-second-row {
		position: relative;
		display: flex;
		align-items: flex-end;
		justify-content: center;
		@include md {
			&:before {
				content: "";
				display: block;
				position: absolute;
				bottom: -20px;
				height: 5px;
				width: 100%;
				background-color: #DFDFDF;
				border-radius: 100%;
				box-shadow:	0px 0px 8px .1px #AFAFAF;
				@include centerX;
			}
			@include xs {
				display: block;
			}
		}
	}

	&-text-col {
		width: 47%;
		margin-left: auto;
		margin-bottom: auto;
		min-height: 525px;
		@include lg {
			width: 50%;
		}
	}

	&-text__title {
		font-size: 50.65px;
		margin-bottom: 10px;
		margin-top: 0;
		text-align: left;
		@include lg {
			font-size: 40px;
			line-height: 1.2;
			@include md {
				font-size: 30px;
				@include xs {
					font-size: 20px;
				}
			}
		}
	}
	&-text__subtitle {
		line-height: 35.47px;
		text-align: left;
		@include lg {
			font-size: 25px;
			line-height: 1.2;
			@include md {
				font-size: 20px;
				@include xs {
					font-size: 15px;
				}
			}
		}
	}

	&-equalizer-right-part-col, &-equalizer-left-part-col {
		padding-bottom: 35px;
		position: relative;
		@include md {
			width: 40%;
			@include xs {
				width: 50%;
			}
		}

		&:after {
			content: "";
			display: block;
			position: absolute;
			bottom: -15px;
			height: 5px;
			width: 100%;
			background-color: #DFDFDF;
			border-radius: 100%;
			box-shadow:	0px 0px 8px .1px #AFAFAF;
			@include centerX;

			@include md {
				display: none;
			}
		}
	}

	&-equalizer-right-part-col {
		float: right
	}
	&-equalizer-left-part-col {
		float: left;
	}
}

.button-engine-start {
	height: 133px;
	width: 133px;
	display: block;
	border: none;
	border-radius: 50%;
	background-color: #D83948;
	background-image: linear-gradient(to bottom, #fb696a, #ed575b, #df454d, #d0313f, #c21631);
	position: relative;
	font-family: $ff-title;
	font-size: 17.93px;
	font-weight: bold;
	color: #fff;
	text-transform: uppercase;
	line-height: 1.2;

	margin-right: 61px;
	margin-left: 61px;

	z-index: 2;

	&:after {
		content: "";
		display: block;
		background: transparent;
		border: 2px solid rgba(#fff, 0.1);
		border-style: groove;
		position: absolute;
		width: 86%;
		height: 86%;
		border-radius: 50%;
		top: 7%;
		left: 7%;
	}

	&:before {
		content: "";
		position: absolute;
		bottom: -15px;
		@include centerX;
		display: block;

		height: 5px;
		width: 100%;
		background-color: #DFDFDF;
		border-radius: 100%;
		box-shadow:	0px 0px 8px .1px #AFAFAF;

		@include md {
			display: none;
		}
	}

	@include lg {
		margin-right: 10px;
		margin-left: 5px;
		height: 100px;
		width: 100px;
		@include md {
			display: block;
			margin-right: 10px;
			margin-left: 10px;
			font-size: 15px;
			@include xs {
				position: absolute;
				margin: 0;
				@include centerX;
				bottom: -30px;
				&:before {
					display: block;
				}
			}
		}
	}
}