.uniqueness {
	position: relative;

	&-wrap {
		position: relative;
		padding-top: 60px;
		padding-left: 135px;
		padding-right: 150px;
		padding-bottom: 437px;

		@include lg {
			padding-left: 50px;
			padding-right: 50px;
			padding-bottom: 330px;
			&:after {
				width: 44%;
			}
			@include md {
				padding-bottom: 250px;
				@include sm {
					padding-bottom: 80px;
					padding-top: 40px;
					@include xs {
						padding-left: 20px;
						padding-right: 20px;
					}
				}
			}
		}
	}

	&__title {
		@include xs {
			line-height: 1.2;
			font-size: 25px;
		}
	}
	
	// compound with previous block
	&:before {
		content: "";
		display: block;
		width: 50px;
		height: 140px;
		position: absolute;
		top: -140px;
		left: 50%;
		transform: translateX(-50px);
		z-index: 1;
		border-bottom: $block-border-settings;
		border-right: $block-border-settings;
		border-bottom-right-radius: $br-block-border;
		@include lg {
			@include md {
				display: none;
			}
		}
	}

	&-items {
		display: flex;
		justify-content: space-around;
		margin-bottom: 18px;
		@include lg {
			align-items: flex-end;

			@include md {
				flex-wrap: wrap;
			}
		}
	}

	&-item-col {
		width: 29%;

		@include md {
			width: 80%;
			@include sm {
				width: 100%;
			}
		}
	}

	&-item {
		display: flex;
		flex-direction: column;
		align-items: center;
		position: relative;
		padding-top: 72px;

		&__number {
			position: absolute;
			text-align: center;
			font-size: 191.59px;
			color: #F0F0F0;
			font-weight: 900;
			font-family: $ff-title;
			z-index: -1;
			//top: -64px;
			top: 0;
			line-height: 1;
		}

		&__title {
			font-size: 18px;
			font-family: $ff-title;
			font-weight: normal;
			text-align: center;
			margin-bottom: 25px;
			position: relative;
			padding-left: 24px;

			&:before {
				content: "";
				display: block;
				width: 10px;
				height: 10px;
				background-color: #3B79E1;
				border-radius: 50%;
				position: absolute;
				left: 0px;
				@include centerY;
			}
		}

		img {
			height: 205px;
			min-width: 100%;
			width: auto;
			box-shadow: 7px 5px 5px #C4C4C4;

			@include lg {
				height: 157px;
				@include md {
					height: auto;
				}
			}
		}
	}

	&-chairs {
		position: absolute;
		bottom: -130px;
		z-index: -5;
		@include centerX;
		width: 100%;

		@include lg {
			bottom: -60px;
			@include md {
				bottom: 0;
				@include sm {
					display: none;
				}
			}
		}
	}

	&__button {
		position: absolute;
		bottom: -30px;
		@include centerX;
		z-index: 5;
	}
}