@media (max-width: 576px){
  .container{
		width: 100%;
		padding: 0px 15px
	}
}
@media (min-width: 576px){
  .container{
		width: 550px
	}
}
@media (min-width: 940px) {
  .container {
		width: 860px
	}
}
@media (min-width: 1200px){
  .container{
		width: 1120px
	}
}
@media (min-width: 1380px){
  .container{
		width: 1300px
	}
}