.footer {

	&-wrap {
		//min-height: 300px;
		padding-left: 60px;
		padding-right: 85px;
		padding-bottom: 35px;
		@include md {
			padding-left: 30px;
			padding-right: 30px;
		}

		&:after {
			border-bottom-right-radius: 0px;
			border-bottom: none;
		}
	}

	&-first-row {
		display: flex;
		padding-top: 45px;
		padding-bottom: 30px;
		clear: both;
		justify-content: flex-end;
		flex-wrap: wrap;
		position: relative;
	}

	&-logo-col {
		@include md {
			width: 100%;
			display: flex;
			justify-content: center;
			margin-bottom: 20px;
		}
		.footer__logo {
			@include centerX;
			height: 100px;
			width: 170px;
			@include md {
				position: static;
				transform: translate(0,0);
				//width: 100%;
			}
		}
	}

	&-social-col {
		margin-right: 25px;
		@include md {
			width: 100%;
			display: flex;
			justify-content: center;
			height: 100%;
			margin-bottom: 20px;
		}
	}

	&-info-col {
		& > div {
			margin-bottom: 13px;
			@include md {
				margin-bottom: 0;
				margin-right: 10px;
				font-size: 15px;
				&:last-child {
					margin-right: 0;
				}
				@include sm {
					margin-bottom: 13px;
					margin-right: 0;
				}
			}
		}

		@include md {
			display: flex;
			width: 100%;
			@include sm {
				flex-direction: column;
				align-items: center;
			}
		}
	}

	&-policy-col {
		text-align: right;
	}

	&-policy {
		margin-right: 5px;
		color: #fff;
		font-size: 16px;
		font-weight: 600;
		display: inline-block;

		&:after {
			content: "";
			display: block;
			height: 1px;
			width: 100%;
			background-color: #fff;
		}
	}

	.nav {
		padding-top: 10px;
		padding-bottom: 0;

		@include xl {
			// background-color: rgba(#fff, 0.5);
			background-color: transparent;
			display: flex; //*
			flex-wrap: wrap;
			overflow: hidden;
			justify-content: flex-start;

			&-mobile_active{
				display: flex;
				transition: 1s;
			}

			&__item {
				margin-bottom: 10px;
				margin-top: 10px;
				@include lg {
					margin-right: 10px;
					@include md {
						padding: 7px 14px;
						font-size: 16px;
						@include xs {
							padding: 5px 10px;
							font-size: 14px;
							margin-top: 5px;
							margin-bottom: 5px;
						}
					}
				}
			}
		}
	}
}

.email-block{
	&_light {
		color: #fff;
	}

	&_light &__img {
		@include sprite($email-light)
	}
}

.phone-block {
	&_light {
		color: #fff;
	}
	

	&_light &__img {
		@include sprite($phone-light)
	}
}

.days-block {
	&_light {
		color: #fff;
		b {
			color: #fff;
		}
		@include md {
			font-size: 15px;
		}
	}
	

	&_light &__img {
		@include sprite($clock-light)
	}
}