body{
	font-family: $ff-main;
	line-height: 1.2;
	font-size: 1rem;
	background-image: url("../img/footer-bg.png");
	background-repeat: no-repeat;
	background-position-y: bottom;
	background-position-x: center; 
}

*:hover{
	transition: 0.3s;
}
*, *:focus{
	outline: 0;
}
html{
	box-sizing: border-box;
}
*, *:after, *:before{
	box-sizing: inherit;
}
ul, ol{
	list-style-type: none;
}
.clearfix{
	&:after{
		content: '';
		display: table;
		clear: both;
	}
}
a{
	text-decoration: none;
}
*{
	margin-top: 0px;
	margin-bottom: 0px;
	margin-left: 0;
	margin-right: 0;
	padding: 0;
	line-height: 1;
}

img {
	max-width: 100%;
}

textarea {
	resize: none;
}