// Qanelas

@font-face {
	font-family: 'Qanelas';
	src: url('../fonts/Qanelas-SemiBold.eot');
	src: local('Qanelas SemiBold'), local('Qanelas-SemiBold'),
			url('../fonts/Qanelas-SemiBold.eot?#iefix') format('embedded-opentype'),
			url('../fonts/Qanelas-SemiBold.woff') format('woff'),
			url('../fonts/Qanelas-SemiBold.ttf') format('truetype');
	font-weight: 600;
	font-style: normal;
}

@font-face {
	font-family: 'Qanelas';
	src: url('../fonts/Qanelas-Black.eot');
	src: local('Qanelas Black'), local('Qanelas-Black'),
			url('../fonts/Qanelas-Black.eot?#iefix') format('embedded-opentype'),
			url('../fonts/Qanelas-Black.woff') format('woff'),
			url('../fonts/Qanelas-Black.ttf') format('truetype');
	font-weight: 900;
	font-style: normal;
}

@font-face {
	font-family: 'Qanelas';
	src: url('../fonts/Qanelas-Regular.eot');
	src: local('Qanelas Regular'), local('Qanelas-Regular'),
			url('../fonts/Qanelas-Regular.eot?#iefix') format('embedded-opentype'),
			url('../fonts/Qanelas-Regular.woff') format('woff'),
			url('../fonts/Qanelas-Regular.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}

