.features {
	position: relative;

	// compound with previous block

	&:before {
		content: "";
		display: block;
		border-left: $block-border-settings;
		border-top: $block-border-settings;
		height: 150px;
		width: 85px;
		border-top-left-radius: $br-block-border;
		@include centerX;
		left: 53%;
		top: -1px;
		box-sizing: content-box;
		@include lg {
			left: 53.7%;
			@include md {
				display: none;
			}
		}
	}

	&:after {
		content: "";
		display: block;
		position: absolute;
		z-index: -19;
		width: 100%;
		height: 100%;
		max-width: 2000px;
		left: 50%;
		transform: translateX(-50%);
		top: 0px;
		background-image: url("../img/features-bg.png");
		background-repeat: no-repeat;
		background-position-x: left;
	}

	&__booklets {
		position: absolute;
		right: -85px;
		bottom: -45px;
		width: 580px;
		min-height: 315px;
		@media (max-width: 1500px) {
			width: 400px;
			right: 0;
			bottom: -100px;
		}
		@include xl {
			width: 400px;
			right: 0;
			bottom: -100px;
			@include lg {
				right: 0px;
				width: 350px;
				@include md {
					display: none;
				}
			}
		}
	}

	&-wrap {
		padding-top: 190px;
		position: relative;
		padding-bottom: 140px;

		@include md {
			padding-top: 50px;
			padding-bottom: 120px;
			@include sm {
				padding-bottom: 140px;
				padding-left: 30px;
			}
		}

		&:before {
			content: "";
			display: block;
			width: 31px;
			height: 31px;
			background-image: linear-gradient(to bottom, #fb6a6a, #ed585b, #df464d, #d1323f, #c31731);
			border-radius: 50%;
			@include centerX;
			top: 155px;
			@include md {
				display: none;
			}
		}
	}

	&-items {
		width: 50%;
		margin-left: auto;
		border-left: $block-border-settings;
		border-left-color: #797A7C;
		padding-bottom: 50px;

		@include sm {
			width: 100%;
			padding-top: 10px;
			padding-bottom: 0;
		}
	}

	&__title {
		margin-bottom: 30px;
		@include md {
			line-height: 1.2;
		}
	}

	&__button {
		@include centerX;
		bottom: 80px;
		@include sm {
			bottom: 50px;
		}
	}

	&-item {
		padding: 20px 0px;
		margin-bottom: 27px;
		padding-left: 40px;
		margin-left: -2px;
		position: relative;
		&__title {
			@include md {
				color: #000;
				font-weight: 600;
				font-size: 18px;
			}
		}

		&__point-img {
			position: absolute;
			left: -14.5px;
			@include centerY;

			&:after {
				content: "";
				display: block;
				width: 3px;
				height: 15px;
				background-color: #000;
				@include centerX;
				position: absolute;
				bottom: -15px;
			}

			&:before {
				content: "";
				display: block;
				width: 3px;
				height: 15px;
				background-color: #000;
				@include centerX;
				position: absolute;
				top: -15px;
				z-index: 1;
			}
		}
	}

}