.container{
	margin: auto;
	width: 1300px;
}

input[type="checkbox"].custom-checkbox {
	display: none;

	& + label {
		display: block;
		width: 20px;
		height: 20px;
		border: 1px solid #fff;
		cursor: pointer;
		padding: 2px;
		position: relative;

		&:after {
			content: "";
			display: block;
			position: absolute;
			height: 22px;
			width: 22px;
			border-radius: 3px;
			border: 1px solid #EF5055;
			@include center;
		}
	}

	&:checked + label {
		background-image: url("../img/tick.svg");
	}
}

.decor-blue-circle {
	*, *:before, *:after {
		transition: 0.5s;
	}
	width: 32px;
	height: 32px;
	background-color: #3B79E1;
	border-radius: 50%;
	display: flex;
	position: relative;
	cursor: pointer;

	&_block-left {
		position: absolute;
		left: -15px;
		@include centerY;
		@include md {
			display: none;
		}
	}

	&_block-right {
		position: absolute;
		right: -15px;
		@include centerY;
		@include md {
			display: none;
		}
	}

	&:before {
		content: "";
		width: 50px;
		height: 50px;
		background-color: transparent;
		position: absolute;
		border: 1px solid rgba(#D8DAE3, 1);
		border-radius: 50%;
		@include center;
	}

	&:after {
		content: "";
		width: 60px;
		height: 60px;
		background-color: transparent;
		position: absolute;
		border: 1px solid rgba(#E1E7EC, 1);
		border-radius: 50%;
		@include center;
	}

	&-center {
		width: 20px;
		height: 20px;
		background-color: #3B79E1;
		border: 5px solid #fff;
		border-radius: 50%;
		margin: auto;

		&:after {
			content: "";
			width: 40px;
			height: 40px;
			background-color: transparent;
			position: absolute;
			border: 1px solid rgba(#CED1E2, 1);
			border-radius: 50%;
			@include center;
		}
	}

	&:hover {
		background-color: transparent;
		&:before {
			width: 23px;
			height: 23px;
		}
		&:after {
			width: 60px;
			height: 60px;
			border-color: transparent;
		}
	}
	&:hover &-center{
		background-color: #000;
		width: 16px;
		height: 16px;
		border: none;

		&:after {
			width: 36px;
			height: 36px;
		}
	}
}

.display-n {
	display: none;
}

.title {
	font-size: 36px;
	color: $fc-main-dark;
	font-family: $ff-title;
	line-height: 64.53px;
	font-weight: bold;
	text-align: center;
}

.subtitle {
	color: $fc-main-lighten-dark;
	font-family: $ff-main;
	font-size: 31.47px;
	line-height: 50.35px;
	text-align: center;
}

.button-main {
	min-width: 214px;
	display: block;
	line-height: 1;
	padding: 25px 23px 20px 23px;
	background-color: #EF5055;
	border: none;
	box-shadow: 10px 20px 50px #F1C0C2;
	border-bottom: 6px solid #AD2327;
	color: #fff;
	font-size: 17.93px;
	font-family: $ff-title;
	font-weight: bold;
	border-radius: 50px;
	cursor: pointer;

	&:hover {
		border-bottom-color: #EF5055;
		// transition: 0s;
		transform: translateY(6px);
	}
}

.section_with-outline-left {
	position: relative;
	&:after {
		z-index: -3;
		content: "";
		display: block;
		position: absolute;
		top: -1px;
		width: 46%;
		left: 0;
		bottom: 0;
		right: 0;

		border: $block-border-settings;
		border-bottom-left-radius: $br-block-border; 
		border-top-left-radius: $br-block-border;
		border-right: none;

		@include md {
			display: none;
		}
	}
}

.section_with-outline-right {
	position: relative;
	&:after {
		z-index: -3;
		content: "";
		display: block;
		position: absolute;
		top: -1px;
		width: 46%;
		//left: 0;
		bottom: 0;
		right: 0;

		border: $block-border-settings;
		border-bottom-right-radius: $br-block-border; 
		border-top-right-radius: $br-block-border;
		border-left: none;

		@include md {
			display: none;
		}
	}
}

.equalizer {
	display: flex;
	//align-items: flex-end;
	background-repeat: no-repeat;
	background-position: left bottom;

	@include md {
		justify-content: space-around;
	}

	&-left {
		background-image: url("../img/equalizer-bg-left.png");
		background-repeat: no-repeat;
		@include lg {
			background-size: cover;
			@include xs {
				background-position: center center;
			}
		}
	}

	&-right {
		background-image: url("../img/equalizer-bg-right.png");
		@include lg {
			background-size: cover;
			@include xs {
				background-position: center center;
			}
		}
		background-repeat: no-repeat;
	}

	&-col {
		margin-right: 8px;
		min-height: 210px;
		display: flex;
		flex-direction: column-reverse;
		justify-content: flex-start;

		width: 19px;

		@include lg {
			width: 18px;
			margin-right: 6px;
			@include md {
				width: 12px;
				margin-right: 0px;
				@include sm {
					width: 11px;
					@include xs {
						width: 9px;
					}
				}
			}
		}
	}

	&-brick {
		height: 11px;
		width: 19px;
		background-color: #FB6368;
		border-radius: 3px;
		margin-bottom: 5px;

		@include lg {
			width: 17px;
			height: 9px;
			@include md {
				width: 12px;
				height: 7px;
				@include sm {
					width: 11px;
					height: 6px;
					@include xs {
						width: 9px;
						height: 5px;
					}
				}
			}
		}

		&:last-child {
			margin-right: 0;
		}

		&:nth-child(1) {
			// background-color: $start-color;
			background-color: #980A20;
			margin-bottom: 0;
		}

		&:nth-child(2) {
			//background-color: lighten($start-color, 2.7%);
			background-color: #A61129;
		}
		
		&:nth-child(3) {
			//background-color: lighten($start-color, 5.4%);
			background-color: #B51932;
		}

		&:nth-child(4) {
			//background-color: lighten($start-color, 8.1%);
			background-color: #C8213C;
		}

		&:nth-child(5) {
			//background-color: lighten($start-color, 10.8%);
			background-color: #D92A46;
		}

		&:nth-child(6) {
			//background-color: lighten($start-color, 13.5%);
			background-color: #E93450;
		}

		&:nth-child(7) {
			//background-color: lighten($start-color, 16.2%);
			background-color: #F23B56;
		}

		&:nth-child(8) {
			//background-color: lighten($start-color, 18.9%);
			background-color: #F6415A;
		}

		&:nth-child(9) {
			// background-color: lighten($start-color, 21.6%);
