.header {
	background-color: #fff;
	border-bottom: 1px solid #A09D9D;
	margin-bottom: 2px;

	&-wrap {
		padding: 23px 60px 0px 60px;

		@include xl {
			padding-left: 0;
			padding-right: 0;
		}
	}

	&-top {
		padding-top: 23px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
	}

	&-logo-col {
		width: 12%;
		img {
			width: 100%;
		}
		@include lg {
			width: 15%;
			@include xs {
				width: 30%;
			}
		}
	}

	&-text-col {
		width: 46%;
		@include lg {
			width: 60%;
			@include md {
				width: 53%;
				@include xs {
					width: 70%;
				}
			}
		}
		//align-self: flex-end;
		//padding-bottom: 5px;
	}

	&-text {

		@include lg {
			padding-left: 20px;
		}
		&__title {
			font-family: $ff-title;
			font-weight: bold;
			color: $fc-main-dark;
			font-size: 27.5px;
			line-height: 35px;
			margin: 0;
			margin-bottom: 5px;

			@include xl {
				font-size: 25px;
				line-height: 1;
				margin: 0;
				margin-bottom: 10px;
				@include md {
					font-size: 20px;
					margin-bottom: 5px;
					@include xs {
						display: none;
					}
				}
			}
		}
		&__subtitle {
			font-size: 20px;
			font-family: $ff-main;
			color: $fc-main-lighten-dark;
			line-height: 27px;
			white-space: nowrap;

			@include xl {
				font-size: 17px;
				line-height: 1;
				@include md {
					white-space: pre-wrap;
				}
			}
		}
	}

	&-language-col {
		width: 100px;
		display: flex;
		align-self: flex-start;
		@include lg {
			width: 20%;
			justify-content: flex-end;
			@include xs {
				padding-top: 10px;
				margin-left: auto;
				width: 50%;
			}
		}
	}

	&-info-col {
		width: 18%;
		& > div {
			margin-bottom: 13px;
		}

		@include lg {
			width: 100%;
			display: flex;
			justify-content: space-around;
			padding-top: 10px;

			@include xs {
				flex-direction: column;
				width: 100%;
				justify-content: space-around;
				& > div {
					width: auto;
					margin-left: auto;
					margin-right: auto;
				}
			}
		}
	}

	&-social-col {
		width: 3%;
		align-self: flex-start;

		@include lg {
			width: 3%;
			@include md {
				width: 6%;
				@include xs {
					padding-top: 10px;
					width: 50%;
					display: flex;
					flex-direction: column;
					align-items: flex-start;
					padding-left: 10px;
				}
			}
		}
	}

	&-bottom {
		display: block;
	}
}

.nav {
	display: flex;
	justify-content: space-between;
	padding: 20px 0px;
	align-items: center;

	&_dark &__item{
		color: #000;

		&_active, &:hover {
			background-color: #EF5055;
			color: #fff;
			font-weight: bold;

			.addition {
				color: #fff;
			}
		}

		.addition {
			color: #EF5055;
		}
	}

	&_light &__item{
		color: #fff;

		&_active, &:hover {
			background-color: #EF5055;
			color: #fff;
			font-weight: bold;

			.addition {
				color: #fff;
			}
		}

		.addition {
			color: #fff;
		}
	}

	&__item {
		padding: 11px 28px;
		border-radius: 20px;
		font-size: 16px;
		font-family: $ff-title;
		font-weight: 500;
		transition: 0.3s;
		@include xl {
			font-size: 15px;
			padding: 11px 25px;
			@include lg {
				font-size: 16px;
			}
		}

		&_active, &:hover {
			transition: 0.3s;
		}

		.addition {
			position: relative;
			top: -5px;
			left: -2px;
			// color: #EF5055;
			font-weight: 900;
		}
	}

	@include lg {
		&-header {
			// background-color: rgba(#fff, 0.5);
			background-color: #fff;
			display: none; //*
			overflow: hidden;
			flex-direction: column;
			align-items: center;
			justify-content: space-around;
		}
		&-mobile_active{
			display: flex;
			transition: 1s;
		}

		&__item {
			margin-bottom: 10px;
			margin-top: 10px;
		}
	}
}

.nav-burger {
	display: none;
	z-index: 50;
	cursor: pointer;
	width: 60px;
	height: 60px;
	//position: absolute;
	position: relative;
	margin-left: auto;
	margin-right: auto;

	// right: 20px;
	// top: 37px;

	@include lg {
		display: block;
	}

	&__line {
		width: 90%;
		height: 5px;
		background-color: #424242;
		display: block;
		@include center;
		transition: .5s;

		//box-shadow: 0px 0px 1px #424242;

		&:before {
			content: "";
			display: block;
			position: absolute;
			width: 100%;
			height: 5px;
			background-color: #424242;
			top: -18px;
			transition: .5s;

			//box-shadow: 0px 0px 1px #424242;
		}

		&:after {
			content: "";
			display: block;
			position: absolute;
			width: 100%;
			height: 5px;
			background-color: #424242;
			top: 18px;
			transition: .5s;

			//box-shadow: 0px 0px 1px #424242;
		}
	}

	&_active {
		.nav-burger__line {
			background-color: rgba(0,0,0,0);
			box-shadow:none;

			&:before {
				top: 0;
				transform: rotate(45deg);
			}
			&:after {
				top: 0;
				transform: rotate(135deg);
			}
		}
	}
}

.bread-crumb {
	width: 8px;
	height: 8px;
	border-radius: 3px;
	background-color: #F2F2F2;
	&_disabled {
		opacity: 0;
	}
};

.social {
	&-inst{
		&_color {
		@include sprite($instagram);
		display: block;
		}

		&_light {
			@include sprite($instagram-light);
			display: block;
		}
	}
}

.email-block {
	display: flex;
		color: #6f6a6a;
		align-items: center;
	&__img {
		@include sprite($email);
		margin-right: 10px;
	}
}

.phone-block {
	display: flex;
		color: #6f6a6a;
		align-items: center;
	&__img {
		@include sprite($phone);
		margin-right: 10px;
	}
}

.days-block {
	display: flex;
	align-items: center;
	color: #6f6a6a;
	&__img {
		@include sprite($clock);
		margin-right: 10px;
	}

	&__all-days {
		display: flex;
		flex-direction: column;
	}

	&__day {
		font-family: $ff-main;
		font-size: 15px;
		line-height: 1.2;
		b {
			font-weight: 600;
			color: #000;
		}
	}
}

.lng-select {
	position: relative;
	width: 100px;
	* {
		transition: 0s;
	}

	&-area {
		padding: 6px 6px 4px 6px;
		background-color: #E4E4E4;
		border-radius: 5px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		cursor: pointer;
		border-bottom: 1px solid transparent;

		&__flag {
			height: 100%;
			width: 22px;
			margin-right: 5px;
		}
		&__descr {
			font-family: $ff-main;
			font-weight: normal;
			font-size: 15px;
		}

		&__arrow {
			color: #000;
			width: 12px;
			height: 12px;
			margin-right: 10px;
			margin-left: 10px;
		}

		&_active {
			border-bottom-left-radius: 0px;
			border-bottom-right-radius: 0px;
			border-bottom: 1px solid #A6A6A6;
		}

		&_active &__arrow {
			transform: rotate(180deg);
		}

		&__content {
			padding: 6px 6px 4px 6px;
			background-color: #E4E4E4;
			border-radius: 5px;
			display: flex;
			align-items: center;
			justify-content: space-between;
			cursor: pointer;
		}
	}

	&__options {
		position: absolute;
		display: none;
		border-bottom-left-radius: 5px;
		border-bottom-right-radius: 5px;

		&_active {
			display: block;
			background-color: #E4E4E4;
			width: 100%;
		}
	}

	&__option {
		display: flex;
		padding: 6px;
		cursor: pointer;
		font-family: $ff-title;
		font-size: 15px;
		align-items: center;

		.option__flag {
			width: 22px;
			height: 100%;
			margin-right: 5px;
		}

		&_active {
			display: none;
		}
	}
}