.order-now {
	background-image: url("../img/order-now-bg.png");
	background-repeat: no-repeat;
	background-position: center center;

	@include lg {
		background-position-y: -70px;
		@include xs {
			background-position-y: -30px;
		}
	}

	&-wrap {
		min-height: 1024px;
		padding-top: 240px;
		position: relative;
		padding-left: 25px;
		padding-right: 25px;

			@include lg {
				padding-top: 100px;
				padding-bottom: 50px;
				padding-top: 40px;
			}

		&:after {
			width: 60%;
			z-index: -99;
		}
	}

	&__title {
		width: 70%;
		line-height: 42.53px;
		margin-left: auto;
		margin-right: auto;
		text-align: center;
		margin-bottom: 50px;

		@include lg {
			width: 90%;
			margin-bottom: 500px;
			font-size: 35px;
			line-height: 1.2;
			@include md {
				font-size: 25px;
				width: 100%;
			}
		}
	}

	&-row {
		display: flex;
		justify-content: space-between;
		@include lg {
			flex-wrap: wrap;
		}
	}

	&-col {
		width: 21%;
		@include xl {
			width: 20%;
			@include lg {
				width: 100%;
				margin-bottom: 50px;
				&:nth-child(2) {
					margin-bottom: 0;
				}
			}
		}

		&:nth-child(2) {
			@include lg {
				order: 1;
			}
		}
	}

	&__button {
		display: flex;
		justify-content: space-around;
		height: 100%;
		align-items: flex-end;
	}

	&-info {
		@include lg {
			display: flex;
			flex-direction: column;
			align-items: center;
			width: 40%;
			margin-left: auto;
			margin-right: auto;
			text-align: center;
			@include md {
				width: 60%;
				@include sm {
					width: 80%;
					@include xs {
						width: 90%;
					}
				}
			}
		}
		&__img {
			margin-bottom: 20px;
			display: flex;
			justify-content: space-around;
			min-height: 140px;
			display: flex;
			align-items: flex-end;
			max-width: 220px;
		}
		&__text {
			font-family: $ff-title;
			line-height: 21px;
		}
	}
}